.spinner {
  display: inline-block;
  width: 80px;
  height: 80px;
  margin: 0 auto;
}
.spinner:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgba(255, 255, 255, 0.7);
  border-color: rgba(255, 255, 255, 0.7) transparent rgba(255, 255, 255, 0.7)
    transparent;
  animation: spinner 1.2s linear infinite;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
